import { useParams } from "react-router-dom";
import { useEffect, useState } from "react";
import { Preloader } from "../components/Preloader";
import "./style.css";

function GamePage({ API_KEY }) {
  const { id } = useParams();
  const [game, setGame] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  const ratingStyles = {
    exceptional: { label: "", color: "gold" },
    recommended: { label: "", color: "green" },
    meh: { label: "", color: "gray" },
    skip: { label: "", color: "red" },
  };

  useEffect(() => {
    setLoading(true);
    setError(null);

    const apiUrl = `https://api.rawg.io/api/games/${id}?key=${API_KEY}`;

    fetch(apiUrl)
      .then((response) => {
        if (!response.ok) throw new Error(`: ${response.status}`);
        return response.json();
      })
      .then((data) => setGame(data))
      .catch((err) => setError(err.message))
      .finally(() => setLoading(false));
  }, [id]);

  if (loading) return <Preloader />;
  if (error) return <p>: {error}</p>;
  if (!game) return <p>  </p>;

  return (
    <div
      className="container-background"
      style={{
        backgroundImage: game.background_image_additional
          ? `url(${game.background_image_additional})`
          : game.background_image
            ? `url(${game.background_image})`
            : "none",
        backgroundSize: "cover",
        backgroundPosition: "center",
        minHeight: "100vh",
      }}
    >
      <div
        className="container-full"
        style={{ padding: "20px", maxWidth: "800px", margin: "0 auto" }}
      >
        <h1 className="GamePage__title">{game.name}</h1>

        {/*   */}
        {game.background_image ? (
          <img
            src={game.background_image}
            alt={game.name}
            style={{
              width: "100%",
              height: "400px",
              objectFit: "cover",
              objectPosition: "top",
              borderRadius: "8px",
              marginBottom: "16px",
            }}
          />
        ) : (
          <div
            style={{
              width: "100%",
              height: "400px",
              backgroundColor: "#ccc",
              borderRadius: "8px",
              marginTop: "16px",
              marginBottom: "16px",
              display: "flex",
              alignItems: "center",
              justifyContent: "center",
            }}
          >
             
          </div>
        )}

        <div className="gamePage-wrapper">
          {/*  */}
          <h5>:</h5>
          {game.developers?.length > 0 ? (
            <div className="developer">
              <p>{game.developers[0].name}</p>
              {game.developers[0].image_background && (
                <img
                  src={game.developers[0].image_background}
                  width="200"
                  alt={game.developers[0].name}
                />
              )}
            </div>
          ) : (
            <p> </p>
          )}

          {/*  */}
          <h5>:</h5>
          {game.ratings?.length > 0 ? (
            <div className="ranking">
              {game.ratings.map((g, index) => {
                const style = ratingStyles[g.title] || {
                  label: g.title,
                  color: "black",
                };
                return (
                  <div
                    className="ranking-item"
                    key={`${g.title}-${index}`}
                    style={{ marginBottom: "8px" }}
                  >
                    <div style={{ color: style.color, fontWeight: "bolder" }}>
                      {style.label}:
                    </div>
                    <div>: {g.count}</div>
                    <div>: {g.percent}%</div>
                  </div>
                );
              })}
            </div>
          ) : (
            <p> </p>
          )}

          {/*  */}
          <h5>:</h5>
          <div className="tags">
            {game.tags?.length > 0 ? (
              game.tags.map((tag) => (
                <a key={tag.id} href="#">
                  {tag.name}
                </a>
              ))
            ) : (
              <p> </p>
            )}
          </div>

          {/*  */}
          <h5>-</h5>
          {game.website ? (
            <a href={game.website} target="_blank" rel="noopener noreferrer">
              {game.website}
            </a>
          ) : (
            <p> </p>
          )}

          {/*  */}
          <h5>:</h5>
          <p>
            <strong> :</strong> {game.released || " "}
          </p>
          <p>
            <strong>:</strong>{" "}
            {game.rating ? `${game.rating} / 5` : " "}
          </p>
          <p>
            <strong>  Metacritic:</strong>{" "}
            {game.metacritic || " "}
          </p>
          <p>
            <strong>:</strong>{" "}
            {game.platforms?.map((p) => p.platform.name).join(", ") ||
              " "}
          </p>
          <p>
            <strong>:</strong>{" "}
            {game.genres?.map((g) => g.name).join(", ") || " "}
          </p>

          {/*  */}
          {game.description_raw && <p>{game.description_raw}</p>}

          <button
            onClick={() => window.history.back()}
            className="GamePage__btn-back"
          >
               
          </button>
        </div>
      </div>
    </div>
  );
}

export { GamePage };
